/* Contenedor principal */
.toggle-container {
  width: 100%;
  max-height: 100vh; 
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2vh 2vh 0 2vh; 
  box-sizing: border-box;
  overflow-y: auto;
  overflow-x: hidden;
}

.toggle-container * {
  box-sizing: border-box;
}


/* Contenedor del botón */
.button-container {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-bottom: 2vh;
}

/* Estilo de los botones */
.toggle-button {
  padding: 0.8vh 1.8vh;
  background-color: #ffffff;
  color: #333;
  border: 2px solid #ddd;
  border-radius: 25px;
  font-size: 1.68vh;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  flex: 1;
  margin: 0 1vh;
}

.toggle-button:hover {
  background-color: #4CAF50;
  color: white;
  border-color: #4CAF50;
}

.toggle-button.active {
  background-color: #4CAF50;
  color: white;
  border-color: #4CAF50;
}

.component-display {
  width: 100%;
  height: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: hidden;
  overflow-x: hidden;
}

.component-display *{
  box-sizing: border-box; 
}

.first-component, .second-component, .fourth-component, .third-component {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.product-list-container {
  width: 100%;
  height: 100%;
}

.second-component {
  background-color: #d9edf7;
}

/* Efecto de transición suave para cambiar entre componentes */
.component-display > div {
  opacity: 0;
  transform: scale(0.95);
  transition: all 0.4s ease;
}

.component-display > div:first-child {
  opacity: 1;
  transform: scale(1);
}

@media (max-width: 600px) {
  .toggle-container {
    padding: 1vh 0.6vh; 
  }

  .toggle-button{
    font-size: 1.4vh;
    letter-spacing: 0.4px;
    margin: 0;
  }
}