.saved-products-container {
  width: 100%;
  height: 100%;
  padding: 0vh 4vw; 
  background-color: white;
  overflow-y: hidden;
}

.saved-products-container * {
  box-sizing: border-box;
}

.container-items-saved {
  max-height: 74vh;
  overflow-y: auto;
  padding: 2vh;
  border-radius: 1vh; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background-color: #fff; 
}

.page-title {
  font-size: 2.4vh;
  text-align: center;
  color: #333;
  padding: 0;
  margin: 1vh 0 2vh 0;
}

.product-item-save-products {
  background: #fff;
  border-radius: 1vh;
  padding: 2vh;
  margin-bottom: 2vh;
  box-shadow: 0 0.5vh 1vh rgba(0, 0, 0, 0.1);
}

.product-title {
  font-size: 1.8vh;
  margin-bottom: 1vh;
  color: #222;
}

/* ID del producto */
.product-id {
  font-size: 1.4vh;
  color: #666;
  margin-bottom: 1vh;
}

/* Título de la sección de plantillas */
.templates-title {
  font-size: 1.6vh;
  color: #333;
}

/* Lista de plantillas: con alto máximo para scroll */
.template-list-asign {
  max-width: 100%;
  list-style: none;
  padding: 0;
  margin: 0 0 2vh 0;
  max-height: 28vh; 
  overflow-y: auto;
  overflow-x: hidden;
}

/* Cada item de plantilla */
.template-item-products-save {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1vh;
  font-size: 1.6vh;
  border-bottom: 0.1vh solid #ddd;
}

.container-buttons-products-saved{
  display: flex;
  flex-direction: row;
  gap: 4vh;
}

/* Botón para eliminar plantilla */
.delete-template-btn {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: transform 0.2s ease, color 0.2s ease;
}

.delete-template-btn:hover {
  transform: scale(1.1); /* Pequeño efecto de escala */
  color: #e60000;
}

.template-list-asign {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Contenedor de la información y botones de movimiento */
.template-info {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Botones para mover plantilla */
.move-buttons {
  display: flex;
  flex-direction: column;
}

.move-template-btn {
  background: none;
  border: none;
  padding: 4px;
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.move-template-btn:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

.move-template-btn:hover:not(:disabled) {
  transform: scale(1.1);
}

.quit-modal-btn{
  background-color: #ff0000;
  color: #fff;
  font-size: 1.4vh;
  border: none;
  padding: 0.8vh 1.2vh;
  margin-top: 2vh;
  margin-left: 6vh;
  font-weight: bold;
  border-radius: 0.2vh;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.quit-modal-btn:hover {
  background-color: #b30000;
}

.open-modal-btn {
  background-color: #007bff;
  color: #fff;
  font-size: 1.4vh;
  border: none;
  padding: 0.8vh 1.2vh;
  font-weight: bold;
  margin-top: 2vh;
  border-radius: 0.2vh;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.open-modal-btn:hover {
  background-color: #0056b3;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  max-width: 400px;
  width: 90%;
}

.templates-checkbox-list {
  margin: 16px 0;
  max-height: 200px;
  overflow-y: auto;
}

.template-checkbox-label {
  display: block;
  margin-bottom: 8px;
  font-size: 1rem;
  cursor: pointer;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 16px;
}

.add-template-btn {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 0.8vh 1.2vh;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-template-btn:hover {
  background-color: #0056b3;
}

.close-modal-btn{
  background-color: #ff0000;
  color: #fff;
  border: none;
  padding: 0.8vh 1.2vh;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.close-modal-btn:hover {
  background-color: #b30000;
}


.error-text {
  color: red;
}

.spinner {
  display: block;
  margin: auto;
}

/* Ajustes responsivos para pantallas más pequeñas */
@media (max-width: 768px) {
  .saved-products-container {
    padding: 2vh 3vw;
  }
  .page-title {
    font-size: 1.6vh;
  }
  .product-item-save-products {
    padding: 2vh;
  }

  .template-id-input,
  .delete-template-btn,
  .add-template-btn {
    font-size: 2vh;
  }

  .add-template-container {
    flex-direction: column;
    gap: 2vh;
  }
}
