/* Estilos para el contenedor de la lista de productos */
.product-list-container {
  width: 100%;
  height: 100%;
  padding: 0vh 4vw; 
  background-color: white;
  overflow-y: hidden;
}

.product-list-container * {
  box-sizing: border-box;
}

/* Título de la lista de productos */
.product-list-title {
  font-size: 2.4vh;
  text-align: center;
  color: #333;
  padding: 0;
  margin: 1vh 0 2vh 0;
}

/* Estilos para cada producto en la lista */
.product-list {
  max-width: 100%;
  list-style: none;
  padding: 0;
  margin: 0 0 2vh 0;
  height: 70vh; 
  overflow-y: auto;
  overflow-x: hidden;
}

.product-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1vh;
  font-size: 1.6vh;
  border-bottom: 0.1vh solid #ddd;
}

.product-title-list {
  font-size: 1.6vh;
  color: #333;
  margin-bottom: 5px;
}

.product-id-list {
  font-size: 1.4vh;
  color: #666;
}

/* Botón para asignar plantilla */
.assign-template-btn {
  background: #007bff;
  color: white;
  padding: 1vh 2vh;
  border: none;
  border-radius: 0.2vh;
  font-size: 1.6vh;
  cursor: pointer;
  font-weight: bold;
  transition: background-color ease-in 0.3s;
}

.assign-template-btn:hover {
  background: #0056b3;
}

/* Estilos para el texto de carga */
.loading-text,
.error-text {
  font-size: 1.2rem;
  text-align: center;
  color: #f00;
}

.loading-text {
  font-style: italic;
}

.error-text {
  color: #cc0000;
}
  