.container-login-button{
  width: 100%;
  height: 100vh;
  padding-top: 20vh;
  display: flex;
  justify-content: start;
  align-items: center;
  background-color: #000000;
  background-image: radial-gradient(#ffffff33 1px, #00091d 1px);
  background-size: 2vh 2vh;
  flex-direction: column;
}


.container-login-button h1{
  color: white;
}

.login-btn {
  background-color: #ffcc00;
  color: #333;               
  padding: 1.2vh 2.4vh;       
  font-size: 1.8vh;
  font-weight: bold;          
  border: none;               
  border-radius: 0.8vh;         
  cursor: pointer;         
  transition: background-color 0.3s ease, transform 0.2s ease;  /* Transiciones suaves */
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1);  /* Sombra suave */
  margin-top: 6vh;          
  width: 100%;              
  max-width: 40vh;          
  display: block;            
  text-align: center;        
}

.login-btn:hover {
  background-color: #ffb600; 
  transform: translateY(-1px);
  box-shadow: 0 0.6vh 1.2vh rgba(0, 0, 0, 0.1); 
}

/* Efecto de focus: cuando el botón tiene el foco */
.login-btn:focus {
  outline: none;             /* Sin borde de enfoque predeterminado */
  box-shadow: 0 0 0 3px rgba(255, 204, 0, 0.5); /* Resaltar con sombra */
}
  